@import url("https://fonts.googleapis.com/css2?family=Geist+Mono:wght@100..900&display=swap");
.app-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px;
  height: 90vh;
}

.board-container {
  grid-column: span 7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 60px;
  background-color: #f0f0f0;
  border-radius: 15px;
}
@media (max-width: 768px) {
  .board-container {
    grid-column: span 12;
  }
}

.config-panel {
  grid-column: span 5;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: #f0f0f0;
  border-radius: 15px;
  padding: 20px;
}
@media (max-width: 768px) {
  .config-panel {
    grid-column: span 12;
  }
}
.config-panel .additional-settings {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.player-panel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px;
  margin: 10px;
}
.player-panel .display-tile {
  height: 100px;
  width: 100px;
  background-color: #fa6666;
  font-size: 20px;
  border-radius: 50%;
}
.player-panel .display-tile.active {
  box-shadow: 0px 0px 2px 2px #000000;
}
.player-panel .display-tile.player-1 {
  background-color: #fa6666;
}
.player-panel .display-tile.player-2 {
  background-color: #d2d282;
}
.player-panel .player-info {
  text-align: start;
  margin-left: 20px;
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cell {
  border-radius: 50%;
  display: inline-block;
  width: 60px;
  height: 60px;
  margin: 3px;
  border: 2px solid #000000;
  background-color: #f0f0f0;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}
.cell:hover {
  background-color: rgb(68.5131578947, 58.4868421053, 58.4868421053);
  transform: scale(1.1);
}
.cell.player-1 {
  background-color: #fa6666;
}
.cell.player-1.winning {
  box-shadow: 0 0 0 2px white;
}
.cell.player-2 {
  background-color: #d2d282;
}
.cell.player-2.winning {
  box-shadow: 0 0 0 2px white;
}

body {
  font-family: "Geist Mono", monospace;
  font-size: 1.6rem;
  color: #000000;
  line-height: 1.6;
  background-color: #f0f0f0;
  text-align: center;
}

button {
  margin: 5px;
  font-family: inherit;
  padding: 12px 24px;
  border: none;
  background-color: #000000;
  color: #f0f0f0;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.4s;
  border-radius: 12px;
}
button:hover {
  transform: scale(1.05);
}
button.outlined {
  background-color: transparent;
  color: #000000;
}
button.outlined:hover {
  background-color: #000000;
  color: #f0f0f0;
}

label {
  font-size: small;
  margin: 0;
}

hr {
  width: 80%;
  height: 1px;
  background-color: black;
  border: none;
}

