.app-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 20px;
  height: 90vh;
}

.board-container {

    @media (max-width: 768px) {
        grid-column: span 12;
    }

  grid-column: span 7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 60px;
  background-color: $color-background;
  border-radius: 15px;
}

.config-panel {

    @media (max-width: 768px) {
        grid-column: span 12;
    }
    
  grid-column: span 5;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: $color-background;
  border-radius: 15px;
  padding: 20px;

  .additional-settings {
    display: flex;
    flex-direction: column;
    padding: 20px;
  }
}

.player-panel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px;
  margin: 10px;

  .display-tile {
    height: 100px;
    width: 100px;
    background-color: $color-player-1;
    font-size: 20px;
    border-radius: 50%;

    &.active {
      box-shadow: 0px 0px 2px 2px $color-text;
    }

    &.player-1 {
      background-color: $color-player-1;
    }

    &.player-2 {
      background-color: $color-player-2;
    }
  }

  .player-info {
    text-align: start;
    margin-left: 20px;
  }
}

.buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
//   width: 100%;
}
